import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  fetchCreateUser,
  fetchExistingUser,
  fetchLogout,
  fetchUpdateUser,
  fetchUserSession,
} from "./authAPI";

const initialState = {
  value: 0,
  status: "",
  userInfo: null,
  isLoggedIn: null,
  loginErrorMessage: null,
  isRouteAct: false,
};

export const createUserAsync = createAsyncThunk(
  "auth/fetchCreateUser",
  async (userData) => {
    const response = await fetchCreateUser(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const existingUserAsync = createAsyncThunk(
  "auth/fetchExistingUser",
  async (loginInfo) => {
    const response = await fetchExistingUser(loginInfo);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchUpdateUserAsync = createAsyncThunk(
  "auth/fetchUpdateUser",
  async (updateUserData) => {
    const response = await fetchUpdateUser(updateUserData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchLogoutAsync = createAsyncThunk(
  "auth/fetchLogout",
  async () => {
    const response = await fetchLogout();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchUserSessionAsync = createAsyncThunk(
  "auth/fetchUserSession",
  async () => {
    const response = await fetchUserSession();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const authSlice = createSlice({
  name: "auth",
  initialState,

  reducers: {
    userLogout: (state, action) => {
      state.userInfo = null;
      state.isLoggedIn = null;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.userInfo = action.payload;
        state.isLoggedIn = {
          role: action.payload.role,
        };
      })
      .addCase(existingUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(existingUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.userInfo = action.payload;
        state.isLoggedIn = {
          role: action.payload.role,
        };
      })
      .addCase(existingUserAsync.rejected, (state, action) => {
        state.status = "idle";
        state.loginErrorMessage = action.error;
        state.isLoggedIn = null;
      })
      .addCase(fetchUpdateUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchUpdateUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.userInfo = action.payload;
      })
      .addCase(fetchUpdateUserAsync.rejected, (state) => {
        state.status = "idle";
      })
      .addCase(fetchLogoutAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchLogoutAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.userInfo = action.payload;
        state.isLoggedIn = action.payload;
      })
      .addCase(fetchLogoutAsync.rejected, (state, action) => {
        state.status = "idle";
      })
      .addCase(fetchUserSessionAsync.pending, (state, action) => {
        state.status = "loading";
        state.isRouteAct = false;
      })
      .addCase(fetchUserSessionAsync.fulfilled, (state, action) => {
        state.userInfo = action.payload;
        state.isLoggedIn = {
          role: action.payload.role,
        };
        state.status = "idle";
        state.isRouteAct = true;
      })
      .addCase(fetchUserSessionAsync.rejected, (state, action) => {
        state.status = "idle";
        state.userInfo = null;
        state.isLoggedIn = null;
        state.isRouteAct = true;
      });
  },
});

export const { increment, userLogout } = authSlice.actions;
export const loginErrorMessage = (state) => state.auth.loginErrorMessage;
export const userInfo = (state) => state.auth.userInfo;
export const isLoggedIndata = (state) => state.auth.isLoggedIn;
export const userStatusData = (state) => state.auth.isRouteAct;
export default authSlice.reducer;
