import { useSelector } from "react-redux";
import Container from "../components/Container";
import { orderData, orderStatusData } from "./orderSlice";
import { Link } from "react-router-dom";
import Navbar from "../navbar/Navbar";

const OrderList = () => {
  const Orders = useSelector(orderData);
  const orderStatus = useSelector(orderStatusData);
  return (
    <>
      <Navbar />
      <Container>
        {/* component */}
        <ul
          role="list"
          className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3"
        >
          {Orders.map((order) =>
            order.items.map((product, index) => (
              <li
                className="col-span-1 divide-y divide-gray-200 rounded-lg bg-white shadow"
                key={order.id + index}
              >
                <div className="flex w-full items-center justify-between space-x-6 p-6">
                  <img
                    className="h-10 w-10 flex-shrink-0  bg-gray-300"
                    src={`${product.thumbnail}`}
                    alt={`${product.title}`}
                  />
                  <div className="flex-1 truncate">
                    <div className="flex items-center space-x-3">
                      <h3 className="truncate text-sm font-medium text-gray-900">
                        {product.title}
                      </h3>
                      <span className="inline-flex flex-shrink-0 items-center rounded-full bg-green-50 px-1.5 py-0.5 text-xs font-medium text-blue-600 ring-1 ring-inset ring-green-600/20 capitalize">
                        {order.status}
                      </span>
                    </div>
                    {/* <p className="mt-1 truncate text-sm text-gray-500">
                      {product.title}
                    </p> */}
                  </div>
                </div>
                <div>
                  <div className="-mt-px flex divide-x divide-gray-200">
                    <div className="-ml-px flex w-0 flex-1">
                      <Link
                        to={`/orderDetail/${order.id}`}
                        className="relative inline-flex w-0 flex-1 items-center justify-center gap-x-3 rounded-br-lg border border-transparent py-4 text-sm font-semibold text-gray-900 cursor-pointer"
                      >
                        View order detail
                      </Link>
                    </div>
                  </div>
                </div>
              </li>
            ))
          )}

          {/* More people... */}
        </ul>
      </Container>
    </>
  );
};
export default OrderList;
