import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  fetchAllOrders,
  fetchOrderCreated,
  fetchOrders,
  fetchUpdateOrder,
} from "./orderAPI";

const initialState = {
  orders: [],
  orderStatus: "idle",
  currentOrder: null,
  totalOrder: [],
  totalOrderCount: 0,
};

export const orderCreatedAsync = createAsyncThunk(
  "order/fetchOrderCreated",
  async (orderData) => {
    const response = await fetchOrderCreated(orderData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const orderDataAsync = createAsyncThunk(
  "order/fetchOrders",
  async (userId) => {
    const response = await fetchOrders(userId);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const AllOrderDataAsync = createAsyncThunk(
  "order/fetchAllOrders",
  async (data) => {
    const response = await fetchAllOrders(data);
    // The value we return becomes the `fulfilled` action payload
    return response;
  }
);
export const fetchUpdateOrderAsync = createAsyncThunk(
  "order/fetchUpdateOrder",
  async (orderData) => {
    const response = await fetchUpdateOrder(orderData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const orderSlice = createSlice({
  name: "order",
  initialState,
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
    deleteCurrentOrder: (state) => {
      state.currentOrder = null;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(orderDataAsync.pending, (state) => {
        state.orderStatus = "loading";
      })
      .addCase(orderDataAsync.fulfilled, (state, action) => {
        state.orderStatus = "idle";
        state.orders = action.payload;
      })
      .addCase(orderCreatedAsync.pending, (state) => {
        state.orderStatus = "loading";
      })
      .addCase(orderCreatedAsync.fulfilled, (state, action) => {
        state.orderStatus = "idle";
        state.orders
          ? state.orders.push(action.payload)
          : (state.orders = [action.payload]);
        state.currentOrder = action.payload;
      })
      .addCase(orderCreatedAsync.rejected, (state, action) => {
        state.orderStatus = "idle";
        // state.value += action.payload;
      })
      .addCase(AllOrderDataAsync.pending, (state) => {
        state.orderStatus = "loading";
      })
      .addCase(AllOrderDataAsync.fulfilled, (state, action) => {
        state.orderStatus = "idle";
        state.totalOrder = action.payload.data;
        state.totalOrderCount = action.payload.totalCount;
      })
      .addCase(fetchUpdateOrderAsync.pending, (state) => {
        state.orderStatus = "loading";
      })
      .addCase(fetchUpdateOrderAsync.fulfilled, (state, action) => {
        state.orderStatus = "idle";
        const index = state.totalOrder.findIndex(
          (order) => order.id === action.payload.id
        );
        state.totalOrder[index] = action.payload;
      });
  },
});

export const { increment, deleteCurrentOrder } = orderSlice.actions;

export const orderData = (state) => state.orders.orders;
export const currenrOrderData = (state) => state.orders.currentOrder;
export const AllOrdersData = (state) => state.orders.totalOrder;
export const totalOrderCount = (state) => state.orders.totalOrderCount;
export const orderStatusData = (state) => state.orders.orderStatus;

export default orderSlice.reducer;
