export function fetchAll(endpoint) {
  let url = "" + endpoint;
  return new Promise(async (resolve) => {
    const response = await fetch(url);
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchAllFilterProducts({
  sort,
  filter,
  pageLimit,
  searchData,
}) {
  let queries = "";
  for (let key in filter) {
    if (filter[key].length > 0) {
      queries += `${key}=${filter[key][filter[key].length - 1]}&`;
    }
  }
  if (sort) {
    queries += `_sort=${sort._sort}&_order=${sort._order}&`;
  }
  if (searchData) {
    queries += `_search=${searchData}&`;
  }
  queries += `_page=${pageLimit._page}&_limit=${pageLimit._limit}&`;

  const url = "/products?" + queries;

  return new Promise(async (resolve) => {
    const response = await fetch(url, { method: "GET", mode: "cors" });
    const data = await response.json();
    const totalCount = response.headers.get("X-Total-Count");
    resolve({ data, totalCount });
  });
}
export function fetchProductByID(id) {
  const url = "/products/" + id;
  return new Promise(async (resolve) => {
    const response = await fetch(url);
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchAddProduct(productInfo) {
  return new Promise(async (resolve) => {
    const response = await fetch("/products", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(productInfo),
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchProductUpdate(updateData) {
  return new Promise(async (resolve) => {
    const response = await fetch("/products/" + updateData.id, {
      method: "PATCH",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(updateData),
    });
    const data = await response.json();
    resolve({ data });
  });
}
