import { Link } from "react-router-dom";
import Container from "../features/components/Container";
import Navbar from "../features/navbar/Navbar";
import { useDispatch, useSelector } from "react-redux";
import { fetchUpdateUserAsync, userInfo } from "../features/auth/authSlice";
import flower from "../assets/flower.jpg";
import { useForm } from "react-hook-form";
import { useState } from "react";

const ProfilePage = () => {
  const [selectedEditAddress, setSelectedEditAddress] = useState({
    index: -1,
    visiblity: "visible",
  });
  const [addAddress, setAddAddress] = useState(false);
  const user = useSelector(userInfo);
  const dispatch = useDispatch();
  const { register, handleSubmit, reset, setValue } = useForm();

  const onSubmitEditAddress = (data, index) => {
    const newAddresses = [...user.addresses];
    newAddresses.splice(index, 1, data);
    const obj = {
      ...user,
      addresses: [...newAddresses],
    }; // Check the form data
    dispatch(fetchUpdateUserAsync(obj)); // Dispatch the action
  };

  const haldeAddressForm = (index) => {
    const addresse = user.addresses[index];
    setValue("name", addresse.name);
    setValue("email", addresse.email);
    setValue("phone", addresse.phone);
    setValue("street", addresse.street);
    setValue("city", addresse.city);
    setValue("state", addresse.state);
    setValue("pin_code", addresse.pin_code);
    setSelectedEditAddress({
      index: index,
      visiblity: "invisible",
    });
  };
  const haldeAddressRemove = (index) => {
    const newAddresses = [...user.addresses];
    newAddresses.splice(index, 1);
    const obj = {
      ...user,
      addresses: [...newAddresses],
    };
    dispatch(fetchUpdateUserAsync(obj));
  };

  const onSubmitAddress = (data) => {
    const obj = {
      ...user,
      addresses: [...user.addresses, data],
    }; // Check the form data
    dispatch(fetchUpdateUserAsync(obj));
    reset(); // Dispatch the action
    setAddAddress(false);
  };

  return (
    <>
      <Navbar />
      <Container>
        <div className="flex flex-col space-y-8">
          <div className="flex flex-col items-center border-b border-gray-500 pb-4 sm:items-center sm:flex-row">
            <div className="relative h-32 w-32   sm:mb-0 mb-3">
              <img
                src={flower}
                alt="aji"
                className=" w-32 h-32 object-cover rounded-2xl"
                loading="lazy"
              />
            </div>
            <div className="flex-auto sm:ml-5 justify-evenly">
              <div className="flex items-center justify-between sm:mt-2">
                <div className="flex items-center">
                  <div className="flex flex-col">
                    <div className="w-full flex-none text-lg text-gray-900 font-bold leading-none">
                      Sarfraj
                    </div>
                    <div className="flex-auto text-gray-400 my-1">
                      <span className="">{user.email}</span>
                      {user.role === "admin" && (
                        <>
                          <span className="mx-3 border-r border-gray-600  max-h-0" />
                          <button className="flex-no-shrink  capitalize bg-red-400 hover:bg-red-500 px-3 py-1 text-xs shadow-sm hover:shadow-lg font-medium tracking-wider border-2 border-red-300 hover:border-red-500 text-white rounded-sm transition ease-in duration-300">
                            {user.role}
                          </button>
                        </>
                      )}
                      <span className="mx-3 border-r border-gray-600  max-h-0" />
                      <Link
                        to={"/orderList"}
                        className="flex-no-shrink bg-gray-400 hover:bg-gray-500 px-3 py-1 text-xs shadow-sm hover:shadow-lg font-medium tracking-wider border-2 border-gray-300 hover:border-gray-500 text-white rounded-sm transition ease-in duration-300"
                      >
                        My orders
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="">
            <div className="flex justify-between">
              <h1 className="text-lg xl:text-xl font-bold tracking-tight text-gray-700">
                My Addresses
              </h1>
              {!addAddress && (
                <button
                  className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                  onClick={() => {
                    setAddAddress(true);
                    setSelectedEditAddress({
                      index: -1,
                      visiblity: "visible",
                    });
                  }}
                >
                  + Add a new address
                </button>
              )}
            </div>
            {addAddress && (
              <div className="mt-4">
                <form noValidate onSubmit={handleSubmit(onSubmitAddress)}>
                  <div className="border-b border-gray-900/10 pb-12">
                    <h2 className="text-xl font-semibold leading-7 text-gray-900">
                      Enter Address
                    </h2>

                    <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                      <div className="sm:col-span-full">
                        <label
                          htmlFor="first-name"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Full name
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register("name", {
                              required: {
                                value: true,
                                message: "Name is required",
                              },
                            })}
                            id="first-name"
                            autoComplete="given-name"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-full">
                        <label
                          htmlFor="email"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Email address
                        </label>
                        <div className="mt-2">
                          <input
                            id="email"
                            {...register("email", {
                              required: {
                                value: true,
                                message: "Email is required",
                              },
                            })}
                            type="email"
                            autoComplete="email"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>
                      <div className="sm:col-span-full">
                        <label
                          htmlFor="m-number"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Mobile Number
                        </label>
                        <div className="mt-2">
                          <input
                            id="m-number"
                            {...register("phone", {
                              required: {
                                value: true,
                                message: "Phone number is required",
                              },
                            })}
                            type="tel"
                            autoComplete="off"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="col-span-full">
                        <label
                          htmlFor="street-address"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Street address
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register("street", {
                              required: {
                                value: true,
                                message: "Street is required",
                              },
                            })}
                            id="street-address"
                            autoComplete="street-address"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-2 sm:col-start-1">
                        <label
                          htmlFor="city"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          City
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register("city", {
                              required: {
                                value: true,
                                message: "City is required",
                              },
                            })}
                            id="city"
                            autoComplete="address-level2"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-2">
                        <label
                          htmlFor="region"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          State
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register("state", {
                              required: {
                                value: true,
                                message: "State is required",
                              },
                            })}
                            id="region"
                            autoComplete="address-level1"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-2">
                        <label
                          htmlFor="postal-code"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Pin code
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register("pin_code", {
                              required: {
                                value: true,
                                message: "PIN code is required",
                              },
                            })}
                            id="postal-code"
                            autoComplete="postal-code"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>
                    </div>
                    <div className="mt-6 flex items-center justify-end gap-x-6">
                      <button
                        type="button"
                        className="text-sm font-semibold leading-6 text-gray-900"
                        onClick={() => setAddAddress(false)}
                      >
                        Cancel
                      </button>
                      <button
                        type="submit"
                        className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                      >
                        Save address
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            )}
            <div className="flex flex-col items-start mt-6 sm:flex-row flex-wrap sm:space-x-4">
              {user.addresses?.map((address, index) => (
                <div
                  className="p-4 mb-6 w-full sm:w-[calc(50%-.5rem)] lg:w-[calc(33%-1rem)]  bg-white rounded-xl shadow-md overflow-hidden"
                  key={index}
                >
                  <div className="flex  justify-between capitalize tracking-wide text-sm text-indigo-500 font-semibold">
                    <span>{address.name}</span> <span>+91 {address.phone}</span>
                  </div>
                  <p className="mt-2 text-gray-500">{address.email}</p>
                  <div className="flex items-center  gap-x-6">
                    <p className=" text-gray-500 flex-1">
                      {`${address.street}, ${address.city}, ${address.state}, ${address.pin_code}`}
                    </p>
                    <div className={`ml-auto ${selectedEditAddress.visiblity}`}>
                      <button
                        type="submit"
                        className={`rounded-md text-sm font-semibold text-indigo-500 shadow-sm hover:text-gray-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2`}
                        onClick={() => haldeAddressForm(index)}
                      >
                        Edit
                      </button>
                      <span className="mx-3 border-r border-gray-600  max-h-0" />
                      <button
                        type="submit"
                        className={`rounded-md  text-sm font-semibold text-indigo-500 shadow-sm hover:text-gray-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2`}
                        onClick={() => haldeAddressRemove(index)}
                      >
                        Remove
                      </button>
                    </div>
                  </div>
                  {selectedEditAddress.index === index && (
                    <form
                      noValidate
                      onSubmit={handleSubmit((data) =>
                        onSubmitEditAddress(data, index)
                      )}
                      className="mt-4 pt-4 border-t border-gray-900/10"
                    >
                      <div className="pb-12">
                        <h2 className="text-xl font-semibold leading-7 text-gray-900">
                          Enter Address
                        </h2>

                        <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8">
                          <div className="sm:col-span-full">
                            <label
                              htmlFor="first-name"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              Full name
                            </label>
                            <div className="mt-2">
                              <input
                                type="text"
                                {...register("name", {
                                  required: {
                                    value: true,
                                    message: "Name is required",
                                  },
                                })}
                                id="first-name"
                                autoComplete="given-name"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                              />
                            </div>
                          </div>

                          <div className="sm:col-span-full">
                            <label
                              htmlFor="email"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              Email address
                            </label>
                            <div className="mt-2">
                              <input
                                id="email"
                                {...register("email", {
                                  required: {
                                    value: true,
                                    message: "Email is required",
                                  },
                                })}
                                type="email"
                                autoComplete="email"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                              />
                            </div>
                          </div>
                          <div className="sm:col-span-full">
                            <label
                              htmlFor="m-number"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              Mobile Number
                            </label>
                            <div className="mt-2">
                              <input
                                id="m-number"
                                {...register("phone", {
                                  required: {
                                    value: true,
                                    message: "Phone number is required",
                                  },
                                })}
                                type="tel"
                                autoComplete="off"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                              />
                            </div>
                          </div>

                          <div className="col-span-full">
                            <label
                              htmlFor="street-address"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              Street address
                            </label>
                            <div className="mt-2">
                              <input
                                type="text"
                                {...register("street", {
                                  required: {
                                    value: true,
                                    message: "Street is required",
                                  },
                                })}
                                id="street-address"
                                autoComplete="street-address"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                              />
                            </div>
                          </div>

                          <div className="sm:col-span-2 sm:col-start-1">
                            <label
                              htmlFor="city"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              City
                            </label>
                            <div className="mt-2">
                              <input
                                type="text"
                                {...register("city", {
                                  required: {
                                    value: true,
                                    message: "City is required",
                                  },
                                })}
                                id="city"
                                autoComplete="address-level2"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                              />
                            </div>
                          </div>

                          <div className="sm:col-span-2">
                            <label
                              htmlFor="region"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              State
                            </label>
                            <div className="mt-2">
                              <input
                                type="text"
                                {...register("state", {
                                  required: {
                                    value: true,
                                    message: "State is required",
                                  },
                                })}
                                id="region"
                                autoComplete="address-level1"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                              />
                            </div>
                          </div>

                          <div className="sm:col-span-2">
                            <label
                              htmlFor="postal-code"
                              className="block text-sm font-medium leading-6 text-gray-900"
                            >
                              Pin code
                            </label>
                            <div className="mt-2">
                              <input
                                type="text"
                                {...register("pin_code", {
                                  required: {
                                    value: true,
                                    message: "PIN code is required",
                                  },
                                })}
                                id="postal-code"
                                autoComplete="postal-code"
                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                              />
                            </div>
                          </div>
                        </div>
                        <div className="mt-6 flex items-center justify-end gap-x-6">
                          <button
                            type="button"
                            className="text-sm font-semibold leading-6 text-gray-900"
                            onClick={() =>
                              setSelectedEditAddress({
                                index: -1,
                                visiblity: "visible",
                              })
                            }
                          >
                            Cancel
                          </button>
                          <button
                            type="submit"
                            className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                          >
                            Save address
                          </button>
                        </div>
                      </div>
                    </form>
                  )}
                </div>
              ))}
            </div>
          </div>
        </div>
      </Container>
    </>
  );
};
export default ProfilePage;
