import React, { Fragment, useRef, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  cartItemsData,
  cartOpen,
  fetchCartUpdateAsync,
  fetchDeleteItemAsync,
  increment,
  incrementAsync,
} from "./cartSlice";
import { Dialog, Transition } from "@headlessui/react";
import { XMarkIcon } from "@heroicons/react/24/outline";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { fetchCartUpdate } from "./cartAPI";
import { discountedPrice } from "../../app/constant";

export function Cart() {
  const cartState = useSelector((state) => state.cart);
  const dispatch = useDispatch();
  const cartItems = useSelector(cartItemsData);
  const navigate = useNavigate();
  const handleQuantity = ({ item, action }) => {
    if (action === "plus") {
      dispatch(
        fetchCartUpdateAsync({ id: item.id, quantity: +item.quantity + 1 })
      );
    } else if (action === "minus" && +item.quantity - 1 > 0) {
      dispatch(
        fetchCartUpdateAsync({ id: item.id, quantity: +item.quantity - 1 })
      );
    } else {
      dispatch(fetchDeleteItemAsync(item.id));
    }
  };
  const totalPrice = cartItems.reduce(
    (price, item) => price + discountedPrice(item.product) * item.quantity,
    0
  );

  return (
    <>
      <Transition.Root show={cartState.isOpen} as={Fragment}>
        <Dialog
          as="div"
          className="relative z-10"
          onClose={() => dispatch(cartOpen(false))}
        >
          <Transition.Child
            as={Fragment}
            enter="ease-in-out duration-500"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="ease-in-out duration-500"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
          </Transition.Child>

          <div className="fixed inset-0 overflow-hidden">
            <div className="absolute inset-0 overflow-hidden">
              <div className="pointer-events-none fixed inset-y-0 right-0 flex max-w-full pl-10">
                <Transition.Child
                  as={Fragment}
                  enter="transform transition ease-in-out duration-500 sm:duration-700"
                  enterFrom="translate-x-full"
                  enterTo="translate-x-0"
                  leave="transform transition ease-in-out duration-500 sm:duration-700"
                  leaveFrom="translate-x-0"
                  leaveTo="translate-x-full"
                >
                  <Dialog.Panel className="pointer-events-auto w-screen max-w-md">
                    <div className="flex h-full flex-col overflow-y-scroll bg-white shadow-xl">
                      <div className="flex-1 overflow-y-auto px-4 py-6 sm:px-6">
                        <div className="sticky -top-6 border border-gray-200 py-3 -mx-4 -my-6 sm:-mx-6 bg-white">
                          <div className="flex items-start justify-between px-4">
                            <Dialog.Title className="text-lg font-medium text-gray-900">
                              Cart ({cartItems.length})
                            </Dialog.Title>
                            <div className="ml-3 flex h-7 items-center">
                              <button
                                type="button"
                                className="relative -m-2 p-2 text-gray-400 hover:text-gray-500"
                                onClick={() => dispatch(cartOpen(false))}
                              >
                                <span className="absolute -inset-0.5" />
                                <span className="sr-only">Close panel</span>
                                <XMarkIcon
                                  className="h-6 w-6"
                                  aria-hidden="true"
                                />
                              </button>
                            </div>
                          </div>
                        </div>

                        <div className="mt-12">
                          <div className="flow-root">
                            <ul
                              role="list"
                              className="-my-6 divide-y divide-gray-200"
                            >
                              {cartItems.length === 0 && (
                                <div className=" mt-[50%] text-center">
                                  <h1 className="text-xl text-gray-500">
                                    Cart is empty
                                  </h1>
                                  <button
                                    type="button"
                                    className="font-s text-indigo-600 hover:text-indigo-500 mt-2.5"
                                    onClick={() => {
                                      dispatch(cartOpen(false));
                                      navigate("/");
                                    }}
                                  >
                                    Continue Shopping
                                    <span aria-hidden="true"> &rarr;</span>
                                  </button>
                                </div>
                              )}
                              {cartItems.map((item, index) => (
                                <li key={index} className="flex py-6">
                                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                    <img
                                      src={item.product.thumbnail}
                                      alt={item.product.title}
                                      className="h-full w-full object-cover object-center"
                                    />
                                  </div>

                                  <div className="ml-4 flex flex-1 flex-col">
                                    <div>
                                      <div className="flex justify-between text-base font-medium text-gray-900">
                                        <h3>
                                          <a>{item.product.title}</a>
                                        </h3>
                                        <p className="ml-4">
                                          ${discountedPrice(item.product)}
                                        </p>
                                      </div>
                                    </div>
                                    <div className="flex flex-1 items-end justify-between text-sm">
                                      <div className="flex items-center text-gray-500 text-xs  justify-between">
                                        <button
                                          type="button"
                                          className="text-sm border border-gray-200 px-2"
                                          onClick={() =>
                                            handleQuantity({
                                              item,
                                              action: "minus",
                                            })
                                          }
                                        >
                                          -
                                        </button>
                                        <span className="px-2.5">
                                          {item.quantity}
                                        </span>
                                        <button
                                          type="button"
                                          className="text-sm border border-gray-200 px-2"
                                          onClick={() =>
                                            handleQuantity({
                                              item,
                                              action: "plus",
                                            })
                                          }
                                        >
                                          +
                                        </button>
                                      </div>

                                      <div className="flex">
                                        <button
                                          type="button"
                                          className="font-medium text-indigo-600 hover:text-indigo-500"
                                          onClick={() =>
                                            handleQuantity({
                                              item,
                                            })
                                          }
                                        >
                                          Remove
                                        </button>
                                      </div>
                                    </div>
                                  </div>
                                </li>
                              ))}
                            </ul>
                          </div>
                        </div>
                      </div>

                      {cartItems.length > 0 && (
                        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                          <div className="flex justify-between text-base font-medium text-gray-900">
                            <p>Subtotal</p>
                            <p>${totalPrice}</p>
                          </div>
                          <p className="mt-0.5 text-sm text-gray-500">
                            Shipping and taxes calculated at checkout.
                          </p>
                          <div className="mt-6">
                            <button
                              onClick={() => {
                                dispatch(cartOpen(false));
                                navigate("/checkout");
                              }}
                              className="flex items-center justify-center w-full rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700 active:scale-95 transition-all"
                            >
                              Checkout
                            </button>
                          </div>
                          <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                            <p>
                              or{" "}
                              <button
                                type="button"
                                className="font-medium text-indigo-600 hover:text-indigo-500"
                                onClick={() => {
                                  dispatch(cartOpen(false));
                                  navigate("/");
                                }}
                              >
                                Continue Shopping
                                <span aria-hidden="true"> &rarr;</span>
                              </button>
                            </p>
                          </div>
                        </div>
                      )}
                    </div>
                  </Dialog.Panel>
                </Transition.Child>
              </div>
            </div>
          </div>
        </Dialog>
      </Transition.Root>
    </>
  );
}
