import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  fetchAddProduct,
  fetchAll,
  fetchAllFilterProducts,
  fetchAllProducts,
  fetchProductByID,
  fetchProductUpdate,
} from "./productListAPI";

const initialState = {
  products: [],
  productCategory: [],
  productBrand: [],
  totalProduct: 0,
  productDetail: null,
  status: "idle",
  search: "",
};

