export const fetchAllCategoryAsync = createAsyncThunk(
  "productList/fetchAllCategory",
  async (categoryEndPoint) => {
    const response = await fetchAll(categoryEndPoint);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchAllBrandAsync = createAsyncThunk(
  "productList/fetchAllbrand",
  async (brandEndPoint) => {
    const response = await fetchAll(brandEndPoint);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchAllFilterProductsAsync = createAsyncThunk(
  "productList/fetchAllFilterProducts",
  async (filterData) => {
    const response = await fetchAllFilterProducts(filterData);
    // The value we return becomes the `fulfilled` action payload
    return response;
  }
);
export const fetchProductByIDAsync = createAsyncThunk(
  "productList/fetchProductByID",
  async (product_ID) => {
    const response = await fetchProductByID(product_ID);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchAddProductAsync = createAsyncThunk(
  "productList/fetchAddProduct",
  async (productInfo) => {
    const response = await fetchAddProduct(productInfo);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchUpdateProductAsync = createAsyncThunk(
  "productList/fetchProductUpdate",
  async (productInfo) => {
    const response = await fetchProductUpdate(productInfo);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const productListSlice = createSlice({
  name: "productList",
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.value += 1;
    },
    clearProductDetail: (state) => {
      state.productDetail = null;
    },
    // Use the PayloadAction type to declare the contents of `action.payload`
    setSearchData: (state, action) => {
      state.search = action.payload;
    },
  },

  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder

      .addCase(fetchAllFilterProductsAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchAllFilterProductsAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.products = action.payload.data;
        state.totalProduct = action.payload.totalCount;
      })
      .addCase(fetchAllBrandAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchAllBrandAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.productBrand = action.payload;
      })
      .addCase(fetchAllCategoryAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchAllCategoryAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.productCategory = action.payload;
      })
      .addCase(fetchProductByIDAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchProductByIDAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.productDetail = action.payload;
      })
      .addCase(fetchAddProductAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchAddProductAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.products.push(action.payload);
      })
      .addCase(fetchUpdateProductAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchUpdateProductAsync.fulfilled, (state, action) => {
        state.status = "idle";
        const index = state.products.findIndex(
          (item) => item.id === action.payload.id
        );
        state.products[index] = action.payload;
      });
  },
});

export const { increment, clearProductDetail, setSearchData } =
  productListSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const allProduct = (state) => state.productList.products;
export const allCategory = (state) => state.productList.productCategory;
export const allBrand = (state) => state.productList.productBrand;
export const totalProduct = (state) => state.productList.totalProduct;
export const productDetail = (state) => state.productList.productDetail;
export const search = (state) => state.productList.search;

