import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchLogoutAsync, isLoggedIndata } from "./authSlice";
import { Navigate } from "react-router-dom";
import { onLogoutCart } from "../cart/cartSlice";
import { notificationFunction } from "../notification/notificationSlice";

const Logout = () => {
  const user = useSelector(isLoggedIndata);
  const dispatch = useDispatch();
  useEffect(() => {
    if (user) {
      dispatch(fetchLogoutAsync());
      dispatch(onLogoutCart());
    }
  });
  if (!user) {
  }
  return <>{!user && <Navigate to="/" replace={true}></Navigate>}</>;
};
export default Logout;
