import { createSlice } from "@reduxjs/toolkit";
import { addToCartAsync, fetchCartUpdateAsync } from "../cart/cartSlice";
import {
  createUserAsync,
  existingUserAsync,
  fetchLogoutAsync,
  fetchUpdateUserAsync,
} from "../auth/authSlice";
import {
  fetchAddProductAsync,
  fetchUpdateProductAsync,
} from "../product-list/productListSlice";

export const initialStateNotification = {
  notification: {
    heading: "",
    content: "",
    bg_color: "",
    text_color: "",
    display: "hidden",
  },
};

export const notificationSlice = createSlice({
  name: "notification",
  initialState: initialStateNotification,
  reducers: {
    notificationFunction: (state, action) => {
      state.notification = action.payload;
    },
  },
  extraReducers: (builder) => {
    // user
    builder.addCase(fetchLogoutAsync.fulfilled, (state, action) => {
      state.notification = {
        heading: "Success",
        content: "Logout successfully!",
        bg_color: "#0B6623",
        text_color: "white",
        display: "block",
      };
    });
    builder.addCase(existingUserAsync.fulfilled, (state, action) => {
      state.notification = {
        heading: "Success",
        content: "Welcome Back",
        bg_color: "#0B6623",
        text_color: "white",
        display: "block",
      };
    });
    builder.addCase(createUserAsync.fulfilled, (state, action) => {
      state.notification = {
        heading: "Success",
        content: "Welcome to shoping",
        bg_color: "#0B6623",
        text_color: "white",
        display: "block",
      };
    });
    builder.addCase(fetchUpdateUserAsync.fulfilled, (state, action) => {
      state.notification = {
        heading: "Success",
        content: "Updated",
        bg_color: "#0B6623",
        text_color: "white",
        display: "block",
      };
    });
    // Cart
    builder.addCase(addToCartAsync.fulfilled, (state, action) => {
      state.notification = {
        heading: "Success",
        content: "Product is added to bag",
        bg_color: "#0B6623",
        text_color: "white",
        display: "block",
      };
    });
    // product
    builder.addCase(fetchUpdateProductAsync.fulfilled, (state, action) => {
      state.notification = {
        heading: "Success",
        content: "Product updated",
        bg_color: "#0B6623",
        text_color: "white",
        display: "block",
      };
    });
    builder.addCase(fetchAddProductAsync.fulfilled, (state, action) => {
      state.notification = {
        heading: "Success",
        content: "Product created",
        bg_color: "#0B6623",
        text_color: "white",
        display: "block",
      };
    });
  },
});

export const { notificationFunction } = notificationSlice.actions;
export const notificationData = (state) => state.notification.notification;

export default notificationSlice.reducer;
