import { Fragment, useEffect, useState } from "react";
import { PAGE_PER_LIMIT } from "../../app/constant";
import Container from "../components/Container";
import Navbar from "../navbar/Navbar";
import { useDispatch, useSelector } from "react-redux";
import {
  AllOrderDataAsync,
  AllOrdersData,
  fetchUpdateOrderAsync,
  totalOrderCount,
} from "../order/orderSlice";
import {
  ChevronDownIcon,
  EyeIcon,
  PencilIcon,
  MinusIcon,
  PlusIcon,
} from "@heroicons/react/24/outline";
import { Menu, Transition, Disclosure, Dialog } from "@headlessui/react";
import { XMarkIcon } from "@heroicons/react/24/outline";

const sortOptions = [
  { title: "New", sort: "date", order: "desc", current: false },
  { title: "Old", sort: "date", order: "asc", current: false },
  {
    title: "Amount High to Low",
    sort: "totalPrice",
    order: "desc",
    current: false,
  },
  {
    title: "Amount Low to High",
    sort: "totalPrice",
    order: "asc",
    current: false,
  },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}
const filters = [
  {
    id: "status",
    title: "Status",
    options: [
      { value: "pending", label: "Pending" },
      { value: "dispatched", label: "Dispatched" },
      { value: "deliverd", label: "Deliverd" },
      { value: "cancelled", label: "Cancelled" },
    ],
  },
];

const AdminOrdersDetail = () => {
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false);
  const [filter, setFilter] = useState();

  const [page, setPage] = useState(1);
  const dispatch = useDispatch();
  const orders = useSelector(AllOrdersData);
  const totalOrder = useSelector(totalOrderCount);
  const [changeStatus, setChangeStatus] = useState(-1);
  const [sort, setSort] = useState(null);
  const pageLimit = { _page: page, _limit: PAGE_PER_LIMIT };
  useEffect(() => {
    dispatch(AllOrderDataAsync({ sort, filter, pageLimit }));
    setPage(1);
    window.scrollTo(0, 0);
  }, [sort, filter, dispatch]);
  const handleSortValue = (e, option) => {
    const newSort = option.sort
      ? { _sort: option.sort, _order: option.order }
      : null;
    setSort(newSort);
  };
  const chooseColor = (status) => {
    switch (status) {
      case "pending":
        return "center relative inline-block select-none whitespace-nowrap rounded-lg bg-amber-500 py-2 px-3.5 align-baseline font-sans text-xs font-bold capitalize leading-none text-black";
      case "dispatched":
        return "center relative inline-block select-none whitespace-nowrap rounded-lg bg-orange-500 py-2 px-3.5 align-baseline font-sans text-xs font-bold capitalize leading-none text-black";
      case "deliverd":
        return "center relative inline-block select-none whitespace-nowrap rounded-lg bg-green-500 py-2 px-3.5 align-baseline font-sans text-xs font-bold capitalize leading-none text-white";
      case "cancelled":
        return "center relative inline-block select-none whitespace-nowrap rounded-lg bg-red-500 py-2 px-3.5 align-baseline font-sans text-xs font-bold capitalize leading-none text-white";
      default:
        return "center relative inline-block select-none whitespace-nowrap rounded-lg bg-amber-500 py-2 px-3.5 align-baseline font-sans text-xs font-bold capitalize leading-none text-black";
    }
  };

  const handleStatus = (e, order) => {
    dispatch(fetchUpdateOrderAsync({ id: order.id, status: e.target.value }));
    setChangeStatus(-1);
  };

  const joinfilters = filter?.status?.join("_");
  const handleFilterValue = (e, filter_name, filter_value) => {
    const newFilters = { ...filter };
    if (e.target.checked) {
      newFilters[filter_name]
        ? newFilters[filter_name].push(filter_value)
        : (newFilters[filter_name] = [filter_value]);
    } else {
      const index = newFilters[filter_name].findIndex(
        (el) => el === filter_value
      );
      newFilters[filter_name].splice(index, 1);
    }
    setFilter(newFilters);
  };

  return (
    <>
      <Navbar />
      <Container>
        <>
          {" "}
          <div className="flex flex-wrap -mx-3 mb-5">
            <div className="w-full max-w-full px-3 mb-6  mx-auto">
              <div className="relative flex-[1_auto] flex flex-col break-words min-w-0 bg-clip-border rounded-[.95rem] bg-white">
                <div className="relative flex flex-col min-w-0 break-words border border-dashed bg-clip-border rounded-2xl border-stone-200 bg-light/30">
                  {/* card header */}
                  <div className="px-4 pt-5 flex justify-between items-center flex-wrap min-h-[70px] pb-0 bg-transparent">
                    <h3 className="flex flex-col items-start justify-center m-2 ml-0 font-medium text-l text-dark">
                      <span className="mr-3 font-bold text-dark">
                        Customer Orders
                      </span>
                    </h3>
                    <div className="flex items-center">
                      <Menu
                        as="div"
                        className="relative inline-block text-left"
                      >
                        <div className="group-hover:border-gray-500 px-3 py-1 rounded-sm">
                          <Menu.Button className="group inline-flex justify-center text-sm font-medium text-gray-700 hover:text-gray-900">
                            Sort
                            <ChevronDownIcon
                              className="-mr-1 ml-1 h-5 w-5 flex-shrink-0 text-gray-400 group-hover:text-gray-500"
                              aria-hidden="true"
                            />
                          </Menu.Button>
                        </div>

                        <Transition
                          as={Fragment}
                          enter="transition ease-out duration-100"
                          enterFrom="transform opacity-0 scale-95"
                          enterTo="transform opacity-100 scale-100"
                          leave="transition ease-in duration-75"
                          leaveFrom="transform opacity-100 scale-100"
                          leaveTo="transform opacity-0 scale-95"
                        >
                          <Menu.Items className="absolute right-0 z-10 mt-2 w-40 origin-top-right rounded-md bg-white shadow-2xl ring-1 ring-black ring-opacity-5 focus:outline-none">
                            <div className="py-1">
                              {sortOptions.map((option) => (
                                <Menu.Item key={option.title}>
                                  {({ active }) => (
                                    <p
                                      onClick={(e) =>
                                        handleSortValue(e, option)
                                      }
                                      className={classNames(
                                        option.current
                                          ? "font-medium text-gray-900"
                                          : "text-gray-500",
                                        active ? "bg-gray-100" : "",
                                        "block px-4 py-2 text-sm cursor-pointer"
                                      )}
                                    >
                                      {option.title}
                                    </p>
                                  )}
                                </Menu.Item>
                              ))}
                            </div>
                          </Menu.Items>
                        </Transition>
                      </Menu>
                      {/* <button
                          type="button"
                          className="-m-2 ml-5 p-2 text-gray-400 hover:text-gray-500 sm:ml-7"
                        >
                          <span className="sr-only">View grid</span>
                          <Squares2X2Icon
                            className="h-5 w-5"
                            aria-hidden="true"
                          />
                        </button> */}
                      <button
                        type="button"
                        className="-m-2 ml-4 p-2 text-gray-400 hover:text-gray-500 sm:ml-6 "
                        onClick={() => setMobileFiltersOpen(true)}
                      >
                        <span className="sr-only">Filters</span>

                        {/* <Set className="h-5 w-5" aria-hidden="true" /> */}
                        <svg
                          class="w-5 h-5 text-gray-800 dark:text-white"
                          aria-hidden="true"
                          xmlns="http://www.w3.org/2000/svg"
                          fill="currentColor"
                          viewBox="0 0 20 20"
                        >
                          <path d="M1 5h1.424a3.228 3.228 0 0 0 6.152 0H19a1 1 0 1 0 0-2H8.576a3.228 3.228 0 0 0-6.152 0H1a1 1 0 1 0 0 2Zm18 4h-1.424a3.228 3.228 0 0 0-6.152 0H1a1 1 0 1 0 0 2h10.424a3.228 3.228 0 0 0 6.152 0H19a1 1 0 0 0 0-2Zm0 6H8.576a3.228 3.228 0 0 0-6.152 0H1a1 1 0 0 0 0 2h1.424a3.228 3.228 0 0 0 6.152 0H19a1 1 0 0 0 0-2Z" />
                        </svg>
                      </button>
                    </div>

                    <Transition.Root show={mobileFiltersOpen} as={Fragment}>
                      <Dialog
                        as="div"
                        className="relative z-11"
                        onClose={setMobileFiltersOpen}
                      >
                        <Transition.Child
                          as={Fragment}
                          enter="transition-opacity ease-linear duration-300"
                          enterFrom="opacity-0"
                          enterTo="opacity-100"
                          leave="transition-opacity ease-linear duration-300"
                          leaveFrom="opacity-100"
                          leaveTo="opacity-0"
                        >
                          <div className="fixed inset-0 bg-black bg-opacity-25" />
                        </Transition.Child>

                        <div className="fixed inset-0 z-40 flex">
                          <Transition.Child
                            as={Fragment}
                            enter="transition ease-in-out duration-300 transform"
                            enterFrom="translate-x-full"
                            enterTo="translate-x-0"
                            leave="transition ease-in-out duration-300 transform"
                            leaveFrom="translate-x-0"
                            leaveTo="translate-x-full"
                          >
                            <Dialog.Panel className="relative ml-auto flex h-full w-full max-w-xs flex-col overflow-y-auto bg-white py-4 pb-12 shadow-xl">
                              <div className="flex items-center justify-between px-4">
                                <h2 className="text-lg font-medium text-gray-900">
                                  Filters
                                </h2>
                                <button
                                  type="button"
                                  className="-mr-2 flex h-10 w-10 items-center justify-center rounded-md bg-white p-2 text-gray-400"
                                  onClick={() => setMobileFiltersOpen(false)}
                                >
                                  <span className="sr-only">Close menu</span>
                                  <XMarkIcon
                                    className="h-6 w-6"
                                    aria-hidden="true"
                                  />
                                </button>
                              </div>

                              {/* Filters */}
                              <form className="mt-4 border-t border-gray-200">
                                <h3 className="sr-only">Categories</h3>

                                {filters.map((section) => (
                                  <Disclosure
                                    as="div"
                                    key={section.id}
                                    className="border-t border-gray-200 px-4 py-6"
                                  >
                                    {({ open }) => (
                                      <>
                                        <h3 className="-mx-2 -my-3 flow-root">
                                          <Disclosure.Button className="flex w-full items-center justify-between bg-white px-2 py-3 text-gray-400 hover:text-gray-500">
                                            <span className="font-medium text-gray-900">
                                              {section.title}
                                            </span>
                                            <span className="ml-6 flex items-center">
                                              {open ? (
                                                <MinusIcon
                                                  className="h-5 w-5"
                                                  aria-hidden="true"
                                                />
                                              ) : (
                                                <PlusIcon
                                                  className="h-5 w-5"
                                                  aria-hidden="true"
                                                />
                                              )}
                                            </span>
                                          </Disclosure.Button>
                                        </h3>
                                        <Disclosure.Panel className="pt-6">
                                          <div className="space-y-6">
                                            {section.options.map(
                                              (option, optionIdx) => (
                                                <div
                                                  key={option.value}
                                                  className="flex items-center"
                                                >
                                                  <input
                                                    id={`filter-mobile-${section.id}-${optionIdx}`}
                                                    title={`${section.id}[]`}
                                                    checked={joinfilters?.includes(
                                                      option.value
                                                    )}
                                                    readOnly
                                                    type="checkbox"
                                                    onClick={(e) =>
                                                      handleFilterValue(
                                                        e,
                                                        section.id,
                                                        option.value
                                                      )
                                                    }
                                                    className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                                  />
                                                  <label
                                                    htmlFor={`filter-mobile-${section.id}-${optionIdx}`}
                                                    className="ml-3 min-w-0 flex-1 text-gray-500"
                                                  >
                                                    {option.label}
                                                  </label>
                                                </div>
                                              )
                                            )}
                                          </div>
                                        </Disclosure.Panel>
                                      </>
                                    )}
                                  </Disclosure>
                                ))}
                              </form>
                            </Dialog.Panel>
                          </Transition.Child>
                        </div>
                      </Dialog>
                    </Transition.Root>
                  </div>
                  {/* end card header */}
                  {/* card body  */}

                  <div className="flex-auto block py-8 pt-6 px-5">
                    <div className="overflow-x-auto">
                      <table className="w-full my-0 align-middle text-dark border-neutral-200">
                        <thead className="align-bottom">
                          <tr className="font-semibold text-[0.85rem] text-secondary-dark">
                            <th className="pb-3 text-center min-w-[50px]">
                              Order ID
                            </th>
                            <th className="pb-3 text-center min-w-[150px]">
                              Products
                            </th>
                            <th className="pb-3 text-center min-w-[50px]">
                              Amount
                            </th>
                            <th className="pb-3 text-center min-w-[100px]">
                              Address
                            </th>
                            <th className="pb-3  text-center min-w-[100px]">
                              Status
                            </th>
                            <th className="pb-3  text-center min-w-[100px]">
                              Date
                            </th>
                            <th className="pb-3  text-center min-w-[100px]">
                              Action
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          {orders.length > 0 ? (
                            orders.map((order, index) => (
                              <tr className="border-b border-dashed last:border-b-0">
                                <td className="p-3 text-start">
                                  <span className="py-3  font-semibold text-[.95rem] leading-none text-primary bg-primary-light">
                                    #{order.id}
                                  </span>
                                </td>
                                <td className="p-3 pl-0">
                                  {order.items.map((product) => (
                                    <div className="flex items-center mb-1">
                                      <div className="relative inline-block shrink-0 rounded-2xl me-3">
                                        <img
                                          src={`${product.thumbnail}`}
                                          className="w-[35px] h-[35px] inline-block shrink-0"
                                          alt=""
                                        />
                                      </div>
                                      <div className="flex flex-col justify-start">
                                        <a className="mb-1">
                                          {" "}
                                          {product.title}{" "}
                                        </a>
                                      </div>
                                    </div>
                                  ))}
                                </td>
                                <td className="p-3 pr-0 text-center">
                                  <span className="px-4 py-3  font-semibold text-[.95rem] leading-none text-primary bg-primary-light">
                                    {order.totalPrice}
                                  </span>
                                </td>
                                <td className="p-3 pr-0 text-start">
                                  <div className="">
                                    <div>
                                      {order.address.name},{" "}
                                      {order.address.email},
                                    </div>
                                    <div>
                                      {order.address.street},{" "}
                                      {order.address.city},
                                    </div>
                                    <div>
                                      {order.address.state},
                                      {order.address.pin_code}
                                    </div>
                                    {order.address.phone}
                                  </div>
                                </td>

                                <td className="p-3 text-center">
                                  {changeStatus === index ? null : (
                                    <div
                                      className={`${chooseColor(order.status)}`}
                                    >
                                      <div className="mt-px">
                                        {order.status}
                                      </div>
                                    </div>
                                  )}

                                  {changeStatus === index && (
                                    <select
                                      id="category"
                                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                      onChange={(e) => handleStatus(e, order)}
                                      value={order.status}
                                    >
                                      {[
                                        "pending",
                                        "dispatched",
                                        "deliverd",
                                        "cancelled",
                                      ].map((status) => (
                                        <option
                                          value={status}
                                          className="capitalize"
                                        >
                                          {status}
                                        </option>
                                      ))}
                                    </select>
                                  )}
                                </td>
                                <td className="pr-0 text-start">
                                  <span className="">
                                    {new Date(order.orderCreatedAt).toString()}
                                  </span>
                                </td>
                                <td className="pr-0 text-center">
                                  <span className="flex justify-center items-center space-x-1">
                                    <span className="cursor-pointer">
                                      {" "}
                                      <EyeIcon className="w-5 h-5"></EyeIcon>{" "}
                                    </span>
                                    <span
                                      className="cursor-pointer"
                                      onClick={() => setChangeStatus(index)}
                                    >
                                      {" "}
                                      <PencilIcon className="w-5 h-5"></PencilIcon>{" "}
                                    </span>
                                  </span>
                                </td>
                              </tr>
                            ))
                          ) : (
                            <div className=" mt-[20%] text-center">
                              <h1 className="text-xl text-gray-500">
                                {" "}
                                No Orders 
                              </h1>
                            </div>
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
              <div className="flex flex-1 justify-between sm:hidden">
                <a
                  href="#"
                  className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
                >
                  Previous
                </a>
                <a
                  href="#"
                  className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
                >
                  Next
                </a>
              </div>
              <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
                <div>
                  <p className="text-sm text-gray-700">
                    Showing{" "}
                    <span className="font-medium">
                      {(page - 1) * PAGE_PER_LIMIT + 1}
                    </span>{" "}
                    to{" "}
                    <span className="font-medium">
                      {totalProductCount > page * PAGE_PER_LIMIT
                        ? page * PAGE_PER_LIMIT
                        : totalProductCount}
                    </span>{" "}
                    of <span className="font-medium">{totalProductCount}</span>{" "}
                    results
                  </p>
                </div>
                <div>
                  <nav
                    className="isolate inline-flex -space-x-px rounded-md shadow-sm"
                    aria-label="Pagination"
                  >
                    <button
                      disabled={page === 1 ? true : false}
                      className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                      onClick={() => setPage((prev) => prev - 1)}
                    >
                      <span className="sr-only">Previous</span>
                      <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
                    </button>

                    {Array(Math.ceil(totalProductCount / PAGE_PER_LIMIT))
                      .fill(1)
                      .map((item, index) => (
                        <p
                          key={index}
                          aria-current="page"
                          className={`relative z-10 inline-flex items-center cursor-pointer ${
                            page === index + 1
                              ? "bg-indigo-600 text-white"
                              : "text-gray-900"
                          }  px-4 py-2 text-sm font-semibold  focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600`}
                          onClick={() => {
                            handlePagination(index + 1);
                          }}
                        >
                          {index + 1}
                        </p>
                      ))}
                    <button
                      disabled={
                        page === totalProductCount / PAGE_PER_LIMIT
                          ? true
                          : false
                      }
                      className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                      onClick={() => setPage((prev) => prev + 1)}
                    >
                      <span className="sr-only">Next</span>
                      <ChevronRightIcon
                        className="h-5 w-5"
                        aria-hidden="true"
                      />
                    </button>
                  </nav>
                </div>
              </div>
            </div>{" "} */}
        </>
      </Container>
    </>
  );
};
export default AdminOrdersDetail;
