
import Container from "../features/components/Container";
import { useDispatch, useSelector } from "react-redux";
import { userInfo } from "../features/auth/authSlice";
import Navbar from "../features/navbar/Navbar";
import { useState } from "react";
import {
  cartItemsData,
  fetchCartUpdateAsync,
  fetchDeleteItemAsync,
} from "../features/cart/cartSlice";
import {
  currenrOrderData,
  orderCreatedAsync,
} from "../features/order/orderSlice";
import { Navigate } from "react-router-dom";
import Address from "../features/components/Address";
import { discountedPrice } from "../app/constant";
import { notificationFunction } from "../features/notification/notificationSlice";

const Checkout = () => {
  const user = useSelector(userInfo);
  const cartItems = useSelector(cartItemsData);
  const currentOrder = useSelector(currenrOrderData);
  const dispatch = useDispatch();
  const [selectedAddress, setSlelectedAddress] = useState(null);
  const [selectedPaymentOption, setSelectedPaymentOption] = useState(null);

  const handleQuantity = ({ item, action }) => {
    if (action === "plus") {
      dispatch(
        fetchCartUpdateAsync({ id: item.id, quantity: +item.quantity + 1 })
      );
    } else if (action === "minus" && +item.quantity - 1 > 0) {
      dispatch(
        fetchCartUpdateAsync({ id: item.id, quantity: +item.quantity - 1 })
      );
    } else {
      dispatch(fetchDeleteItemAsync(item.id));
    }
  };
  const totalPrice = cartItems.reduce(
    (price, item) => price + discountedPrice(item.product) * item.quantity,
    0
  );
  const handleOrder = () => {
    if (!selectedAddress) {
      dispatch(
        notificationFunction({
          heading: "Error",
          content: "Address is not selected!",
          bg_color: "#e50000",
          text_color: "white",
          display: "block",
        })
      );
    } else if (!selectedPaymentOption) {
      dispatch(
        notificationFunction({
          heading: "Error",
          content: "Payment method is not selected!",
          bg_color: "#e50000",
          text_color: "white",
          display: "block",
        })
      );
    } else {
      dispatch(
        orderCreatedAsync({
          address: selectedAddress,
          payment: selectedPaymentOption,
          totalPrice,
          items: cartItems.map((item) => ({
            ...item.product,
            quantity: item.quantity,
          })),
          status: "pending",
        })
      );
    }
  };

  return (
    <>
      {currentOrder && currentOrder.payment === "COD" && (
        <Navigate to={`/order-success/${currentOrder.id}`}></Navigate>
      )}
      {currentOrder && currentOrder.payment === "card" && (
        <Navigate to={`/stripe-checkout`}></Navigate>
      )}
      <Navbar />
      <Container>
        <div className="grid lg:grid-cols-5 gap-12">
          <div className="col-span-3 lg:px-4 lg:py-2 lg:bg-white">
            <div className="space-y-12">
              <Address />
              {user.addresses.length > 0 && (
                <div className="border-b border-gray-900/10 pb-12">
                  <h2 className="text-xl font-semibold leading-7 text-gray-900">
                    Existing Address
                  </h2>

                  <ul role="list" className="divide-y divide-gray-100">
                    {user.addresses.map((address, index) => (
                      <label
                        key={index}
                        className="flex justify-between gap-x-6 py-5 cursor-pointer"
                        htmlFor={`address-${index}`}
                      >
                        <div className="flex min-w-0 gap-x-4 items-center">
                          <input
                            id={`address-${index}`}
                            name="address_option"
                            onChange={() => setSlelectedAddress(address)}
                            type="radio"
                            className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                          />
                          <div className="min-w-0 flex-auto">
                            <p className="text-sm font-semibold leading-6 text-gray-900">
                              {address.name}
                            </p>
                            <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                              {address.email}
                            </p>
                          </div>
                        </div>
                        <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                          <p className="text-sm leading-6 text-gray-900">
                            {address.phone}
                          </p>

                          <p className="mt-1 text-xs leading-5 text-gray-500">
                            {`${address.street}, ${address.city}, ${address.state}, ${address.pin_code}`}
                          </p>
                        </div>
                      </label>
                    ))}
                  </ul>
                </div>
              )}
              <div className="pb-8">
                <div className="mt-10 space-y-10">
                  <fieldset>
                    <legend className="text-sm font-semibold leading-6 text-gray-900">
                      Payment Option
                    </legend>
                    {/* <p className="mt-1 text-sm leading-6 text-gray-600">
                These are delivered via SMS to your mobile phone.
              </p> */}
                    <div className="mt-6 space-y-6">
                      <div className="flex items-center gap-x-3">
                        <input
                          id="payment_opt_1"
                          name="payment_option"
                          onChange={(e) =>
                            setSelectedPaymentOption(e.target.value)
                          }
                          type="radio"
                          value="card"
                          className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                        />
                        <label
                          htmlFor="payment_opt_1"
                          className="block text-sm font-medium leading-6 text-gray-900 cursor-pointer"
                        >
                          Credit/Debit card
                        </label>
                      </div>
                      <div className="flex items-center gap-x-3">
                        <input
                          id="payment_opt_2"
                          name="payment_option"
                          type="radio"
                          onChange={(e) =>
                            setSelectedPaymentOption(e.target.value)
                          }
                          value="UPI"
                          className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                        />
                        <label
                          htmlFor="payment_opt_2"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          UPI
                        </label>
                      </div>
                      <div className="flex items-center gap-x-3">
                        <input
                          id="COD"
                          name="payment_option"
                          type="radio"
                          onChange={(e) =>
                            setSelectedPaymentOption(e.target.value)
                          }
                          value="COD"
                          className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                        />
                        <label
                          htmlFor="COD"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          COD
                        </label>
                      </div>
                    </div>
                  </fieldset>
                </div>
              </div>
            </div>
          </div>
          <div className=" col-span-3 lg:col-span-2 lg:px-4 lg:py-2 lg:bg-white">
            <div className="">
              {/* <div className="flow-root"> */}
              <ul role="list" className="-mt-6 divide-y divide-gray-200">
                {cartItems.map((item, index) => (
                  <li key={index} className="flex py-6">
                    <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                      <img
                        src={item.product.thumbnail}
                        alt={item.product.title}
                        className="h-full w-full object-cover object-center"
                      />
                    </div>

                    <div className="ml-4 flex flex-1 flex-col">
                      <div>
                        <div className="flex justify-between text-base font-medium text-gray-900">
                          <h3>
                            <a>{item.product.title}</a>
                          </h3>
                          <p className="ml-4">
                            ${discountedPrice(item.product)}
                          </p>
                        </div>
                      </div>
                      <div className="flex flex-1 items-end justify-between text-sm">
                        <div className="flex items-center text-gray-500 text-xs  justify-between">
                          {/* Qty {product.quantity} */}
                          <button
                            type="button"
                            className="text-sm border border-gray-200 px-2"
                            onClick={() =>
                              handleQuantity({
                                item,
                                action: "minus",
                              })
                            }
                          >
                            -
                          </button>
                          <span className="px-2.5">{item.quantity}</span>
                          <button
                            type="button"
                            className="text-sm border border-gray-200 px-2"
                            onClick={() =>
                              handleQuantity({
                                item,
                                action: "plus",
                              })
                            }
                          >
                            +
                          </button>
                        </div>

                        <div className="flex">
                          <button
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                            onClick={() =>
                              handleQuantity({
                                item,
                              })
                            }
                          >
                            Remove
                          </button>
                        </div>
                      </div>
                    </div>
                  </li>
                ))}
              </ul>
              {/* </div> */}
            </div>
            <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
              <div className="flex justify-between text-base font-medium text-gray-900">
                <p>Subtotal</p>
                <p>${totalPrice}</p>
              </div>
              <p className="mt-0.5 text-sm text-gray-500">
                Shipping and taxes calculated at checkout.
              </p>
              <div className="mt-6">
                <div
                  onClick={handleOrder}
                  className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700 cursor-pointer"
                >
                  Place Order
                </div>
              </div>
              <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                <p>
                  or{" "}
                  <button
                    type="button"
                    className="font-medium text-indigo-600 hover:text-indigo-500 active:scale-95 transition-all"
                  >
                    Continue Shopping
                    <span aria-hidden="true"> &rarr;</span>
                  </button>
                </p>
              </div>
            </div>
          </div>
        </div>
      </Container>
    </>
  );
};

export default Checkout;
