import React, { Fragment, useEffect, useRef, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  allBrand,
  allCategory,
  allProduct,
  allProductsAsync,
  fetchAllBrandAsync,
  fetchAllCategoryAsync,
  fetchAllFilterProductsAsync,
  search,
  totalProduct,
} from "./productListSlice";
import { Dialog, Disclosure, Menu, Transition } from "@headlessui/react";
import { XMarkIcon } from "@heroicons/react/24/outline";
import {
  ChevronDownIcon,
  FunnelIcon,
  MinusIcon,
  PlusIcon,
  Squares2X2Icon,
} from "@heroicons/react/20/solid";
import { ChevronLeftIcon, ChevronRightIcon } from "@heroicons/react/20/solid";
import { Link } from "react-router-dom";
import { StarIcon } from "@heroicons/react/24/solid";
import { PAGE_PER_LIMIT, discountedPrice } from "../../app/constant";
import Navbar from "../navbar/Navbar";

const sortOptions = [
  // { title: 'Most Popular', href: '#', current: true },
  { title: "Best Rating", sort: "rating", order: "desc", current: false },
  // { title: 'Newest', href: '#', current: false },
  { title: "Price: Low to High", sort: "price", order: "asc", current: false },
  { title: "Price: High to Low", sort: "price", order: "desc", current: false },
];
const subCategories = [
  { title: "Totes", href: "#" },
  { title: "Backpacks", href: "#" },
  { title: "Travel Bags", href: "#" },
  { title: "Hip Bags", href: "#" },
  { title: "Laptop Sleeves", href: "#" },
];

const items = [
  {
    id: 1,
    title: "Back End Developer",
    department: "Engineering",
    type: "Full-time",
    location: "Remote",
  },
  {
    id: 2,
    title: "Front End Developer",
    department: "Engineering",
    type: "Full-time",
    location: "Remote",
  },
  {
    id: 3,
    title: "User Interface Designer",
    department: "Design",
    type: "Full-time",
    location: "Remote",
  },
];
function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

function ProductList() {
  const products = useSelector(allProduct);
  const Categories = useSelector(allCategory);
  const Brands = useSelector(allBrand);
  const totalProductCount = useSelector(totalProduct);
  const searchData = useSelector(search);
  const dispatch = useDispatch();
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false);
  const [filter, setFilter] = useState({});
  const [sort, setSort] = useState();
  const [page, setPage] = useState(1);
  const pageLimit = { _page: page, _limit: PAGE_PER_LIMIT };

  useEffect(() => {
    dispatch(fetchAllBrandAsync("/brands"));
    dispatch(fetchAllCategoryAsync("/categories"));
  }, []);

  // useEffect(() => {
  //   dispatch(allProductsAsync({ pageLimit }));
  //   window.scrollTo(0, 0);
  // }, [page]);

  useEffect(() => {
    dispatch(
      fetchAllFilterProductsAsync({ sort, filter, pageLimit, searchData })
    );
    window.scrollTo(0, 0);
  }, [sort, filter, page, searchData]);

  useEffect(() => {
    setPage(1);
  }, [totalProductCount, searchData]);
  const filters = [
    {
      id: "category",
      title: "Category",
      options: Categories,
    },
    {
      id: "brand",
      title: "Brands",
      options: Brands,
    },
  ];
  let joinFilter = "";
  joinFilter += filter?.brand?.join("_");
  joinFilter += filter?.category?.join("_");
  const handleFilterValue = (e, filter_name, filter_value) => {
    const newFilters = { ...filter };
    if (e.target.checked) {
      newFilters[filter_name]
        ? newFilters[filter_name].push(filter_value)
        : (newFilters[filter_name] = [filter_value]);
    } else {
      const index = newFilters[filter_name].findIndex(
        (el) => el === filter_value
      );
      newFilters[filter_name].splice(index, 1);
    }
    setFilter(newFilters);
  };
  const handleSortValue = (e, option) => {
    const newSort = option.sort
      ? { _sort: option.sort, _order: option.order }
      : null;
    setPage(1);
    setSort(newSort);
  };
  const handlePagination = (pageNo) => {
    setPage(pageNo);
  };

  return (
    <>
      <div className="bg-white">
        <div>
          {/* Mobile filter dialog */}
          <Transition.Root show={mobileFiltersOpen} as={Fragment}>
            <Dialog
              as="div"
              className="relative z-40 lg:hidden"
              onClose={setMobileFiltersOpen}
            >
              <Transition.Child
                as={Fragment}
                enter="transition-opacity ease-linear duration-300"
                enterFrom="opacity-0"
                enterTo="opacity-100"
                leave="transition-opacity ease-linear duration-300"
                leaveFrom="opacity-100"
                leaveTo="opacity-0"
              >
                <div className="fixed inset-0 bg-black bg-opacity-25" />
              </Transition.Child>

              <div className="fixed inset-0 z-40 flex">
                <Transition.Child
                  as={Fragment}
                  enter="transition ease-in-out duration-300 transform"
                  enterFrom="translate-x-full"
                  enterTo="translate-x-0"
                  leave="transition ease-in-out duration-300 transform"
                  leaveFrom="translate-x-0"
                  leaveTo="translate-x-full"
                >
                  <Dialog.Panel className="relative ml-auto flex h-full w-full max-w-xs flex-col overflow-y-auto bg-white py-4 pb-12 shadow-xl">
                    <div className="flex items-center justify-between px-4">
                      <h2 className="text-lg font-medium text-gray-900">
                        Filters
                      </h2>
                      <button
                        type="button"
                        className="-mr-2 flex h-10 w-10 items-center justify-center rounded-md bg-white p-2 text-gray-400"
                        onClick={() => setMobileFiltersOpen(false)}
                      >
                        <span className="sr-only">Close menu</span>
                        <XMarkIcon className="h-6 w-6" aria-hidden="true" />
                      </button>
                    </div>

                    {/* Filters */}
                    <form className="mt-4 border-t border-gray-200">
                      <h3 className="sr-only">Categories</h3>

                      {filters.map((section) => (
                        <Disclosure
                          as="div"
                          key={section.id}
                          className="border-t border-gray-200 px-4 py-6"
                        >
                          {({ open }) => (
                            <>
                              <h3 className="-mx-2 -my-3 flow-root">
                                <Disclosure.Button className="flex w-full items-center justify-between bg-white px-2 py-3 text-gray-400 hover:text-gray-500">
                                  <span className="font-medium text-gray-900">
                                    {section.title}
                                  </span>
                                  <span className="ml-6 flex items-center">
                                    {open ? (
                                      <MinusIcon
                                        className="h-5 w-5"
                                        aria-hidden="true"
                                      />
                                    ) : (
                                      <PlusIcon
                                        className="h-5 w-5"
                                        aria-hidden="true"
                                      />
                                    )}
                                  </span>
                                </Disclosure.Button>
                              </h3>
                              <Disclosure.Panel className="pt-6">
                                <div className="space-y-6">
                                  {section.options.map((option, optionIdx) => (
                                    <div
                                      key={option.value}
                                      className="flex items-center"
                                    >
                                      <input
                                        id={`filter-mobile-${section.id}-${optionIdx}`}
                                        title={`${section.id}[]`}
                                        checked={joinFilter.includes(
                                          option.value
                                        )}
                                        readOnly
                                        type="checkbox"
                                        onClick={(e) =>
                                          handleFilterValue(
                                            e,
                                            section.id,
                                            option.value
                                          )
                                        }
                                        className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                      />
                                      <label
                                        htmlFor={`filter-mobile-${section.id}-${optionIdx}`}
                                        className="ml-3 min-w-0 flex-1 text-gray-500 cursor-pointer"
                                      >
                                        {option.label}
                                      </label>
                                    </div>
                                  ))}
                                </div>
                              </Disclosure.Panel>
                            </>
                          )}
                        </Disclosure>
                      ))}
                    </form>
                  </Dialog.Panel>
                </Transition.Child>
              </div>
            </Dialog>
          </Transition.Root>

          <main className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
            <div className="flex items-baseline justify-between border-b border-gray-200 pb-6 pt-20">
              <h1 className="text-2xl xl:text-3xl font-bold tracking-tight text-gray-900">
                All Products
              </h1>

              <div className="flex items-center">
                <Menu as="div" className="relative inline-block text-left">
                  <div>
                    <Menu.Button className="group inline-flex justify-center text-sm font-medium text-gray-700 hover:text-gray-900">
                      Sort
                      <ChevronDownIcon
                        className="-mr-1 ml-1 h-5 w-5 flex-shrink-0 text-gray-400 group-hover:text-gray-500"
                        aria-hidden="true"
                      />
                    </Menu.Button>
                  </div>

                  <Transition
                    as={Fragment}
                    enter="transition ease-out duration-100"
                    enterFrom="transform opacity-0 scale-95"
                    enterTo="transform opacity-100 scale-100"
                    leave="transition ease-in duration-75"
                    leaveFrom="transform opacity-100 scale-100"
                    leaveTo="transform opacity-0 scale-95"
                  >
                    <Menu.Items className="absolute right-0 z-10 mt-2 w-40 origin-top-right rounded-md bg-white shadow-2xl ring-1 ring-black ring-opacity-5 focus:outline-none">
                      <div className="py-1">
                        {sortOptions.map((option) => (
                          <Menu.Item key={option.title}>
                            {({ active }) => (
                              <p
                                // href={option.href}
                                onClick={(e) => handleSortValue(e, option)}
                                className={classNames(
                                  option.current
                                    ? "font-medium text-gray-900"
                                    : "text-gray-500",
                                  active ? "bg-gray-100" : "",
                                  "block px-4 py-2 text-sm cursor-pointer"
                                )}
                              >
                                {option.title}
                              </p>
                            )}
                          </Menu.Item>
                        ))}
                      </div>
                    </Menu.Items>
                  </Transition>
                </Menu>
                <button
                  type="button"
                  className="-m-2 ml-4 p-2 text-gray-400 hover:text-gray-500 sm:ml-6 lg:hidden"
                  onClick={() => setMobileFiltersOpen(true)}
                >
                  <span className="sr-only">Filters</span>
                  <svg
                    className="w-5 h-5 text-gray-800 dark:text-white"
                    aria-hidden="true"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path d="M1 5h1.424a3.228 3.228 0 0 0 6.152 0H19a1 1 0 1 0 0-2H8.576a3.228 3.228 0 0 0-6.152 0H1a1 1 0 1 0 0 2Zm18 4h-1.424a3.228 3.228 0 0 0-6.152 0H1a1 1 0 1 0 0 2h10.424a3.228 3.228 0 0 0 6.152 0H19a1 1 0 0 0 0-2Zm0 6H8.576a3.228 3.228 0 0 0-6.152 0H1a1 1 0 0 0 0 2h1.424a3.228 3.228 0 0 0 6.152 0H19a1 1 0 0 0 0-2Z" />
                  </svg>
                </button>
              </div>
            </div>

            <section aria-labelledby="products-heading" className="pb-24 pt-6">
              <div className="grid grid-cols-1 gap-x-8 gap-y-10 lg:grid-cols-4 pb-10">
                {/* Filters */}
                <form className="hidden lg:block">
                  <h3 className="sr-only">Categories</h3>
                  {filters.map((section) => (
                    <Disclosure
                      as="div"
                      key={section.id}
                      className="border-b border-gray-200 py-6"
                    >
                      {({ open }) => (
                        <>
                          <h3 className="-my-3 flow-root">
                            <Disclosure.Button className="flex w-full items-center justify-between bg-white py-3 text-sm text-gray-400 hover:text-gray-500">
                              <span className="font-medium text-gray-900">
                                {section.title}
                              </span>
                              <span className="ml-6 flex items-center">
                                {open ? (
                                  <MinusIcon
                                    className="h-5 w-5"
                                    aria-hidden="true"
                                  />
                                ) : (
                                  <PlusIcon
                                    className="h-5 w-5"
                                    aria-hidden="true"
                                  />
                                )}
                              </span>
                            </Disclosure.Button>
                          </h3>
                          <Disclosure.Panel className="pt-6">
                            <div className="space-y-4">
                              {section.options.map((option, optionIdx) => (
                                <div
                                  key={option.value}
                                  className="flex items-center"
                                >
                                  <input
                                    id={`filter-${section.id}-${optionIdx}`}
                                    title={`${section.id}[]`}
                                    defaultValue={option.value}
                                    type="checkbox"
                                    checked={joinFilter.includes(option.value)}
                                    readOnly
                                    onChange={(e) =>
                                      handleFilterValue(
                                        e,
                                        section.id,
                                        option.value
                                      )
                                    }
                                    className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                  />
                                  <label
                                    htmlFor={`filter-${section.id}-${optionIdx}`}
                                    className="ml-3 text-sm text-gray-600"
                                  >
                                    {option.label}
                                  </label>
                                </div>
                              ))}
                            </div>
                          </Disclosure.Panel>
                        </>
                      )}
                    </Disclosure>
                  ))}
                </form>

                {/* Product grid */}
                <div className="lg:col-span-3">
                  <div className="bg-white">
                    <div className="mx-auto max-w-2xl px-4 sm:px-6  lg:max-w-7xl lg:px-8">
                      {/* <h2 className="text-2xl font-bold tracking-tight text-gray-900">Customers also purchased</h2> */}

                      <div className="mt-6 grid grid-cols-1  gap-y-10 sm:grid-cols-2 lg:grid-cols-3 xl:">
                        {products.map(
                          (product, index) =>
                            !product.deleted && (
                              <div
                                key={product.id}
                                className="relative lg:hover:shadow-lg p-4"
                              >
                                <div className="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-md lg:aspect-none lg:h-80">
                                  <img
                                    src={product.thumbnail}
                                    alt={product.title}
                                    className="h-full w-full object-cover object-center lg:h-full lg:w-full"
                                  />
                                </div>
                                <div className="mt-4 flex justify-between">
                                  <div>
                                    <h3 className="text-sm text-gray-700">
                                      <Link
                                        to={`/product-detail/${product.id}`}
                                      >
                                        <span
                                          aria-hidden="true"
                                          className="absolute inset-0"
                                        />
                                        {product.title}
                                      </Link>
                                    </h3>
                                    <p className=" flex items-center  mt-1 text-green-600">
                                      <StarIcon className="w-4 mr-1" />{" "}
                                      <span className="text-">
                                        {product.rating}
                                      </span>{" "}
                                    </p>
                                  </div>
                                  <div className="text-sm font-medium text-gray-900">
                                    <div>${discountedPrice(product)}</div>
                                    {/* <span className='text-xs text-green-700'>{ product.discountPercentage}% off</span> */}
                                    <span className="line-through text-xs text-gray-400 mx-2">
                                      ${product.price}
                                    </span>
                                  </div>
                                </div>
                              </div>
                            )
                        )}
                      </div>
                    </div>
                  </div>
                </div>
                {/* end of product grid */}
              </div>
              <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
                <div className="flex flex-1 justify-between sm:hidden">
                  <button
                    disabled={page === 1 ? true : false}
                    className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
                    onClick={() => setPage((prev) => prev - 1)}
                  >
                    Previous
                  </button>
                  <button
                    className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
                    disabled={
                      page === totalProductCount / PAGE_PER_LIMIT ? true : false
                    }
                    onClick={() => setPage((prev) => prev + 1)}
                  >
                    Next
                  </button>
                </div>
                <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
                  <div>
                    <p className="text-sm text-gray-700">
                      Showing{" "}
                      <span className="font-medium">
                        {(page - 1) * PAGE_PER_LIMIT + 1}
                      </span>{" "}
                      to{" "}
                      <span className="font-medium">
                        {totalProductCount > page * PAGE_PER_LIMIT
                          ? page * PAGE_PER_LIMIT
                          : totalProductCount}
                      </span>{" "}
                      of{" "}
                      <span className="font-medium">{totalProductCount}</span>{" "}
                      results
                    </p>
                  </div>
                  <div>
                    <nav
                      className="isolate inline-flex -space-x-px rounded-md shadow-sm"
                      aria-label="Pagination"
                    >
                      <button
                        disabled={page === 1 ? true : false}
                        className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                        onClick={() => setPage((prev) => prev - 1)}
                      >
                        <span className="sr-only">Previous</span>
                        <ChevronLeftIcon
                          className="h-5 w-5"
                          aria-hidden="true"
                        />
                      </button>
                      {/* Current: "z-10 bg-indigo-600 text-white focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600", Default: "text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:outline-offset-0" */}
                      {Array(Math.ceil(totalProductCount / PAGE_PER_LIMIT))
                        .fill(1)
                        .map((item, index) => (
                          <p
                            key={index}
                            aria-current="page"
                            className={`relative z-10 inline-flex items-center cursor-pointer ${
                              page === index + 1
                                ? "bg-indigo-600 text-white"
                                : "text-gray-900"
                            }  px-4 py-2 text-sm font-semibold  focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600`}
                            onClick={() => {
                              handlePagination(index + 1);
                            }}
                          >
                            {index + 1}
                          </p>
                        ))}
                      <button
                        disabled={
                          page === totalProductCount / PAGE_PER_LIMIT
                            ? true
                            : false
                        }
                        className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                        onClick={() => setPage((prev) => prev + 1)}
                      >
                        <span className="sr-only">Next</span>
                        <ChevronRightIcon
                          className="h-5 w-5"
                          aria-hidden="true"
                        />
                      </button>
                    </nav>
                  </div>
                </div>
              </div>
            </section>
          </main>
        </div>
      </div>
    </>
  );
}

export default ProductList;
