import { useDispatch, useSelector } from "react-redux";
import { notificationData, notificationFunction } from "./notificationSlice";

const NotificationBadge = () => {
  const dispatch = useDispatch();
  const { content, heading, bg_color, text_color, display } =
    useSelector(notificationData);
  const closNotification = () => {
    setTimeout(() => {
      dispatch(
        notificationFunction({
          heading: "",
          content: "",
          bg_color: "",
          text_color: "",
          display: "hidden",
        })
      );
    }, 1500);
  };
  if (display === "block") {
    closNotification();
  }

  return (
    <div
      className={`fixed top-[15%] z-10 translate-x-[-50%]  left-[50%] ${display} `}
    >
      <>
        <div
          className={`font-regular relative block w-full max-w-screen-md rounded-lg  px-2 py-2 text-base text-${text_color}`}
          data-dismissible="alert"
          style={{ backgroundColor: `${bg_color}`, opacity: 1 }}
        >
          <div className="ml-6 mr-10">
            <p
              className={`mt-2 block font-sans text-base font-normal leading-relaxed text-${text_color} antialiased`}
            >
              <span className="font-sans text-l font-semibold leading-snug tracking-normal">
                {" "}
                {heading} :{" "}
              </span>
              {content}
            </p>
          </div>
          <div
            data-dismissible-target="alert"
            data-ripple-dark="true"
            className="absolute top-3 right-3 w-max rounded-lg transition-all hover:bg-white hover:bg-opacity-20"
          >
            <div
              role="button"
              className="w-max rounded-lg p-1"
              onClick={closNotification}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-4 w-4"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                strokeWidth={2}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </div>
          </div>
        </div>
      </>
    </div>
  );
};
export default NotificationBadge;
