import { createBrowserRouter } from "react-router-dom";
import LoginPage from "./pages/LoginPage";
import SignupPage from "./pages/SignupPage";
import Checkout from "./pages/Checkout";
import ProductDetailPage from "./pages/ProductDetailPage";
import Home from "./pages/Home";
import ProtectedRoutes from "./app/ProtectedRoutes";
import PageNotFound from "./pages/PageNotFound";
import OrderSuccess from "./pages/OrderSuccess";
import OrderList from "./features/order/OrderList";
import Order from "./features/order/Order";
import ProfilePage from "./pages/ProfilePage";
import Logout from "./features/auth/Logout";
import { ForgotPassword } from "./features/auth/ForgotPassword";
import AdminProductDetailPage from "./pages/AdminProductDetailPage";
import AdminProtectedRoutes from "./app/ProtectedRoutesAdmin";
import AdminHome from "./pages/AdminHome";
import AdminProductForm from "./features/admin/AdminProductForm";
import AdminOrdersDetail from "./features/admin/AdminOrdersDetail";
import StripeCheckout from "./pages/StripeCheckout";

export const router = createBrowserRouter([
  {
    path: "/",
    element: <Home />,
  },
  {
    path: "/login",
    element: <LoginPage />,
  },
  {
    path: "/signup",
    element: <SignupPage />,
  },
  {
    path: "/checkout",
    element: (
      <ProtectedRoutes>
        <Checkout />
      </ProtectedRoutes>
    ),
  },
  {
    path: "/product-detail/:product_ID",
    element: <ProductDetailPage />,
  },
  {
    path: "/order-success/:id",
    element: <OrderSuccess />,
  },
  {
    path: "/orderList",
    element: (
      <ProtectedRoutes>
        <OrderList />
      </ProtectedRoutes>
    ),
  },
  {
    path: "/orderDetail/:id",
    element: (
      <ProtectedRoutes>
        <Order />
      </ProtectedRoutes>
    ),
  },
  {
    path: "/my_profile",
    element: (
      <ProtectedRoutes>
        <ProfilePage />
      </ProtectedRoutes>
    ),
  },
  {
    path: "/stripe-checkout",
    element: (
      <ProtectedRoutes>
        <StripeCheckout />
      </ProtectedRoutes>
    ),
  },
  {
    path: "/logout",
    element: <Logout />,
  },
  {
    path: "/forgotPassword",
    element: <ForgotPassword />,
  },
  {
    path: "/admin",
    element: (
      <AdminProtectedRoutes>
        <AdminHome />
      </AdminProtectedRoutes>
    ),
  },
  {
    path: "/admin/productDetailPage",
    element: (
      <AdminProtectedRoutes>
        <AdminProductDetailPage />
      </AdminProtectedRoutes>
    ),
  },
  {
    path: "/admin/editProductForm/:productId",
    element: (
      <AdminProtectedRoutes>
        <AdminProductForm />
      </AdminProtectedRoutes>
    ),
  },
  {
    path: "/admin/orders",
    element: (
      <AdminProtectedRoutes>
        <AdminOrdersDetail />
      </AdminProtectedRoutes>
    ),
  },
  // {
  //   path: "/admin/",
  //   element: <AdminProductDetailPage />,
  // },
  { path: "*", element: <PageNotFound /> },
]);
