export function fetchAllOrders({ sort, filter, pageLimit }) {
  let queries = "";
  for (let key in filter) {
    if (filter[key].length > 0) {
      queries += `${key}=${filter[key][filter[key].length - 1]}&`;
    }
  }
  // /products?_page=1&_limit=1&category=smartphones&brand=Apple&_sort=rating&_order=desc&
  if (sort) {
    queries += `_sort=${sort._sort}&_order=${sort._order}&`;
  }
  queries += `_page=${pageLimit._page}&_limit=${pageLimit._limit}&`;
  const url = "/orders/all?" + queries;
  return new Promise(async (resolve) => {
    const response = await fetch(url);
    const data = await response.json();
    const totalCount = response.headers.get("X-Total-Count");
    resolve({ data, totalCount });
  });
}
