import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  fetchAddToCart,
  fetchCartData,
  fetchCartUpdate,
  fetchDeleteCart,
  fetchDeleteItem,
} from "./cartAPI";

const initialState = {
  isOpen: false,
  status: "idle",
  cartItems: [],
};

export const addToCartAsync = createAsyncThunk(
  "cart/fetchAddToCart",
  async (itemInfo) => {
    const response = await fetchAddToCart(itemInfo);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const cartDataAsync = createAsyncThunk(
  "cart/fetchCartData",
  async (useId) => {
    const response = await fetchCartData(useId);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchCartUpdateAsync = createAsyncThunk(
  "cart/fetchCartUpdate",
  async (udateData) => {
    const response = await fetchCartUpdate(udateData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchDeleteItemAsync = createAsyncThunk(
  "cart/fetchDeleteItem",
  async (itemId) => {
    const response = await fetchDeleteItem(itemId);
    // The value we return becomes the `fulfilled` action payload
    return response.itemId;
  }
);
export const fetchDeleteCartAsync = createAsyncThunk(
  "cart/fetchDeleteCart",
  async (userId) => {
    const response = await fetchDeleteCart(userId);
    return response.itemId;
  }
);

export const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    cartOpen: (state, action) => {
      state.isOpen = action.payload;
    },
    onLogoutCart: (state, action) => {
      state.cartItems = [];
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(cartDataAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(cartDataAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.cartItems = action.payload;
      })
      .addCase(addToCartAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(addToCartAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.cartItems.push(action.payload);
      })
      .addCase(fetchCartUpdateAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchCartUpdateAsync.fulfilled, (state, action) => {
        state.status = "idle";
        const index = state.cartItems.findIndex(
          (item) => item.id === action.payload.id
        );
        state.cartItems[index] = action.payload;
      })
      .addCase(fetchDeleteItemAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchDeleteItemAsync.fulfilled, (state, action) => {
        state.status = "idle";
        const index = state.cartItems.findIndex(
          (item) => item.id === action.payload
        );
        state.cartItems.splice(index, 1);
      })
      .addCase(fetchDeleteCartAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchDeleteCartAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.cartItems = [];
      });
  },
});

export const { cartOpen, onLogoutCart } = cartSlice.actions;
export const cartItemsData = (state) => state.cart.cartItems;

export default cartSlice.reducer;
