import { useEffect } from "react";
import { useParams } from "react-router-dom";
import {
  allBrand,
  allCategory,
  clearProductDetail,
  fetchAddProductAsync,
  fetchProductByIDAsync,
  fetchUpdateProductAsync,
  productDetail,
} from "../product-list/productListSlice";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";
import Container from "../components/Container";

const AdminProductForm = () => {
  const { productId } = useParams();
  const dispatch = useDispatch();
  const product = useSelector(productDetail);
  const Categories = useSelector(allCategory);
  const Brands = useSelector(allBrand);
  const { register, handleSubmit, reset, setValue } = useForm();
  useEffect(() => {
    if (productId && productId != "null") {
      dispatch(fetchProductByIDAsync(productId));
    }
    return () => dispatch(clearProductDetail());
  }, [productId, dispatch]);

  useEffect(() => {
    if (product) {
      setValue("title", product.title);
      setValue("description", product.description);
      setValue("brand", product.brand);
      setValue("category", product.category);
      setValue("price", product.price);
      setValue("discountPercentage", product.discountPercentage);
      setValue("stock", product.stock);
      setValue("thumbnail", product.thumbnail);
      setValue("image1", product?.images[0]);
      setValue("image2", product?.images[1]);
      setValue("image3", product?.images[2]);
    }
  }, [product]);

  return (
    <Container>
      <form
        noValidate
        onSubmit={handleSubmit((data) => {
          const productInfo = {
            ...data,
            images: [data.thumbnail, data.image1, data.image2, data.image3],
            price: +data.price,
            discountPercentage: +data.discountPercentage,
            stock: +data.stock,
            id: product.id,
          };
          delete productInfo.image1;
          delete productInfo.image2;
          delete productInfo.image3;
          if (productId && product) {
            dispatch(fetchUpdateProductAsync(productInfo));
            
          } else {
            dispatch(fetchAddProductAsync(productInfo));
          }
        })}
      >
        <div className="border-b border-gray-900/10 pb-12 pt-8">
          <h2 className="text-xl font-semibold leading-7 text-gray-900">
            Enter Product Data
          </h2>

          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            <div className="sm:col-span-full">
              <label
                htmlFor="title"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Title
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("title", {
                    required: {
                      value: true,
                      message: "title is required",
                    },
                  })}
                  id="title"
                  autoComplete="given-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-full">
              <label
                htmlFor="description"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Description
              </label>
              <div className="mt-2">
                <textarea
                  id="description"
                  {...register("description", {
                    required: {
                      value: true,
                      message: "Description is required",
                    },
                  })}
                  rows={3}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  defaultValue={""}
                />
              </div>
            </div>
            <div className="col-span-full">
              <label
                htmlFor="brand"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Brand
              </label>
              <div className="mt-2">
                <select
                  id="brand"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("brand", {
                    required: {
                      value: true,
                      message: "Brand is required",
                    },
                  })}
                >
                  <option>--Select brand--</option>
                  {Brands.map((brand) => (
                    <option value={brand.value} className="capitalize">
                      {brand.label}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            <div className="col-span-full">
              <label
                htmlFor="category"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Category
              </label>
              <div className="mt-2">
                <select
                  id="category"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("category", {
                    required: {
                      value: true,
                      message: "Category is required",
                    },
                  })}
                >
                  <option>--select category--</option>
                  {Categories.map((category) => (
                    <option
                      value={category.value}
                      className="capitalize"
                      key={category.value}
                    >
                      {category.label}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            <div className="col-span-full">
              <label
                htmlFor="price"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Price
              </label>
              <div className="mt-2">
                <input
                  type="number"
                  {...register("price", {
                    required: {
                      value: true,
                      message: "Price is required",
                    },
                    min: 0,
                  })}
                  id="price"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="col-span-full">
              <label
                htmlFor="discountPercentage"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                DiscountPercentage
              </label>
              <div className="mt-2">
                <input
                  type="number"
                  {...register("discountPercentage", {
                    required: {
                      value: true,
                      message: "DiscountPercentage is required",
                    },
                    min: 0,
                    max: 100,
                  })}
                  id="discountPercentage"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="col-span-full">
              <label
                htmlFor="stock"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Stock
              </label>
              <div className="mt-2">
                <input
                  type="number"
                  {...register("stock", {
                    required: {
                      value: true,
                      message: "Stock is required",
                    },
                    min: 0,
                  })}
                  id="stock"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
            <div className="sm:col-span-full">
              <label
                htmlFor="thumbnail"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Thumbnail (url)
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("thumbnail", {
                    required: {
                      value: true,
                      message: "Thumbnail is required",
                    },
                  })}
                  id="thumbnail"
                  autoComplete="given-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
            <div className="sm:col-span-full">
              <label
                htmlFor="image1"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Image 1 (url)
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("image1", {
                    required: {
                      value: true,
                      message: "Image 1 is required",
                    },
                  })}
                  id="image1"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
            <div className="sm:col-span-full">
              <label
                htmlFor="image2"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Image 2 (url)
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("image2", {
                    required: {
                      value: true,
                      message: "Image 2 is required",
                    },
                  })}
                  id="image2"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
            <div className="sm:col-span-full">
              <label
                htmlFor="image3"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Image 3 (url)
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register("image3", {
                    required: {
                      value: true,
                      message: "Image 3 is required",
                    },
                  })}
                  id="image3"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
          </div>
          <div className="mt-6 flex items-center justify-end gap-x-6">
            <button
              type="button"
              className="text-sm font-semibold leading-6 text-gray-900"
            >
              Cancel
            </button>
            {product && product.deleted ? (
              <button
                type="button"
                className="rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-red-600"
                onClick={() => {
                  dispatch(
                    fetchUpdateProductAsync({
                      id: product?.id,
                      deleted: false,
                    })
                  );
                }}
              >
                Restore
              </button>
            ) : (
              <button
                type="button"
                className="rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-red-600"
                onClick={() => {
                  dispatch(
                    fetchUpdateProductAsync({
                      id: product.id,
                      deleted: true,
                    })
                  );
                }}
              >
                Archive
              </button>
            )}
            <button
              type="submit"
              className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Save
            </button>
          </div>
        </div>
      </form>
    </Container>
  );
};
export default AdminProductForm;
