import React, { useEffect } from "react";

import ProductList from "./features/product-list/ProductList";
import Navbar from "./features/navbar/Navbar";
import LoginPage from "./pages/LoginPage";
import SignupPage from "./pages/SignupPage";
import { router } from "./routers";
import ProductDetailPage from "./pages/ProductDetailPage";
import { useDispatch, useSelector } from "react-redux";
import {
  fetchUserSessionAsync,
  isLoggedIndata,
  userStatusData,
} from "./features/auth/authSlice";
import { cartDataAsync } from "./features/cart/cartSlice";
import { RouterProvider } from "react-router-dom";
import Cartpage from "./pages/Cartpage";
import { orderDataAsync } from "./features/order/orderSlice";
import Footer from "./features/Footer";
import NotificationBadge from "./features/notification/NotificatioBadge";

function App() {
  const user = useSelector(isLoggedIndata);
  const userStatus = useSelector(userStatusData);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchUserSessionAsync());
  }, []);
  useEffect(() => {
    if (user) {
      dispatch(cartDataAsync());
      dispatch(orderDataAsync());
    }
  }, [dispatch, user]);
  return (
    <div className="App">
      <NotificationBadge />
      {userStatus && (
        <RouterProvider router={router}>
          <Navbar />
          <Cartpage />
          <ProductDetailPage />
          <ProductList />
          <LoginPage />
          <SignupPage />
        </RouterProvider>
      )}
      <Footer />
    </div>
  );
}

export default App;
